import React, {Component} from "react";

class AgeStates extends Component {
    timeSince(date) {
        let today = new Date().getTime();
        let otherDate = new Date(date).getTime();
        let diff = Math.abs(today - otherDate);

        let days = Math.floor(diff / (1000*3600*24));
        let years = Math.floor(days/365);
        days -= years*365;
        let months = Math.floor(years/30);
        days -= months*30;

        return `${years} years ${months} months ${days} days!`;
    }

    render() {
        return (
            <div>
                <h3 className="mt-4 text-center">{this.timeSince(this.props.date)}</h3>
            </div>
        )
    }
}

export default AgeStates;